.screen {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    opacity: 0;
    pointer-events: none;
}


.screenZoom .screen {
    -webkit-animation: zoomMe 2s linear;
            animation: zoomMe 2s linear;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    pointer-events: all;
}

.screenOut .screen {
    -webkit-animation: zoomMe 5s linear;
            animation: zoomMe 5s linear;
    animation-direction: reverse;
}

.screenOut .planets {
    top: 0px;
    left: 0px;
    transform: scale(1);
    transition: all 2s;
}


@-webkit-keyframes zoomMe {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }
    60% {
        opacity: 0.6;
    }
    70% {
        opacity: 0.7;
    }
    80% {
        opacity: 0.8;
    }
    90% {
        opacity: 0.9;
    }

    100% {
        opacity: 1;
    }
}


@keyframes zoomMe {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }
    60% {
        opacity: 0.6;
    }
    70% {
        opacity: 0.7;
    }
    80% {
        opacity: 0.8;
    }
    90% {
        opacity: 0.9;
    }

    100% {
        opacity: 1;
    }
}
